@import "normalizez.scss";
@import "resets.scss";

* {
    box-sizing: border-box;
    font-family: 'Marcellus', serif; }


.container {
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    object-fit: cover; }

.main-section {
    display: flex;
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) ), url('../img/bg.image.png');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-bottom: 58px;



    &__right-wrapper {
        margin-top: 23px;
        width: 50%;
        margin-left: 42px; }
    &__left-wrapper {
        width: 50%;
        margin-top: 23px; }
    &__title {
        font-size: 63px;
        color: #fff;
        margin-bottom: 51px; }
    &__nav-menu-right {
        display: flex;
        margin-bottom: 69px; }
    &__nav-menu-left {
        display: flex;
        justify-content: end;
        margin-right: 42px;
        margin-bottom: 620px; }
    &__nav-menu {
        margin-right: 42px;
        &:last-child {
            margin-right: 0; }
        a {
            text-decoration: none;
            color: #fff;
            font-size: 14px;
            &:hover {
                color: #000000;
                transition: 0.5s;
                text-decoration: underline; } } } }


.button {
    position: relative;
    display: block;
    border: 2px solid #1F1E1E;
    border-radius: 4.16667px;
    width: 200px;
    height: 37px;
    color: #fff;
    cursor: pointer;
    background: #141806;
    transition: 0.5s;
    left: 0;
    &:hover {
        background: #676B4A;
        transition: 0.5s;
        width: 202px;
        color: #000; } }

.option {
    text-transform: uppercase;
    margin-bottom: 40px;
    &__flex {
        display: flex;
        justify-content: space-between;
        width: 1216px;
        margin: 0 auto;
        li {
            text-align: start;
            font-size: 13px;
            line-height: 15px; }
        span {
            position: relative;
            left: 40px;
            font-size: 30px; } } }

.new-drops {
    &__title {
        text-align: center;
        font-size: 125px;
        margin-bottom: 68px; } }


.second-section {
    &__button {
        display: block;
        margin: 0 auto;
        margin-bottom: 142px;
        left: 0;
        &:hover {
            background: #fff; } }
    &__our-mission {
        &_text {
            text-transform: uppercase;
            font-size: 13px;
            margin-bottom: 16px;
            text-align: center; }

        &_title {
            font-size: 40px;
            text-align: center;
            margin: 0 auto;
            line-height: 46px;
            margin-bottom: 96px; } } }

.catalog {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-items: center;
    margin-bottom: 60px;
    img {
        margin-bottom: 20px; }
    figcaption {
        font-size: 13px;
        text-align: center;
        text-transform: uppercase; } }

.line {
    width: 1300px;
    background: rgba(31, 30, 30, 0.1);
    margin: 0 auto;
    border-width: 0.01px; }

.third-section {
