            margin-right: 0; } }
    &_title {
        color: #425E44;
        text-align: center;
        font-size: 52px;
        line-height: 58px;
        margin-top: 150px;
        margin-bottom: 157px; }
    &_text {
        text-align: center;
        text-transform: uppercase;
